  const filteredTemplates = FUNNEL_TEMPLATES.filter(template => {
    const matchesCategory = selectedCategory === 'all' || template.category === selectedCategory
    const matchesSearch = template.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
                         template.description.toLowerCase().includes(searchQuery.toLowerCase())
    return matchesCategory && matchesSearch
  })
  
  // Handle create funnel
  const handleCreateFunnel = () => {
    if (!funnelName.trim()) {
      alert('Please enter a funnel name')
      return
    }
    
    // In production, this would make an API call to create the funnel
    // For now, redirect to the funnel builder with a mock ID
    const newFunnelId = `funnel-${Date.now()}`
    router.push(`/sub-account/digital-hub/funnels/${newFunnelId}`)
  }
  
